/*
 * Sidebar Styles
 */

.sidebar__header {
  @include section-header();
}

.sidebar__title {
  @include section-title();
}

.widget {
  border-bottom: 1px solid $border-light;
  padding-bottom: rem(20);
}

.widget__title {
  @include section-subtitle();
  margin-bottom: spacer(1);

  &.widget__title--hidden {
    @include screen-reader-text;
  }
}

.widget__title--slack::after,
.widget__title--twitter::after {
  background: center no-repeat;
  background-size: 100% auto;
  content: '';
  display: inline-block;

  @supports (mask-image: url()) {
    background-color: $steel;
    mask-position: center;
    mask-repeat: no-repeat;
    mask-size: 100% auto;
  }
}

.widget__title--slack::after {
  background-image: url('../../images/icon-slack.svg');
  height: rem(22);
  width: rem(22);

  @supports (mask-image: url()) {
    background-image: none;
    mask-image: url('../../images/icon-slack.svg');
  }
}

.widget__title--twitter::after {
  background-image: url('../../images/icon-twitter.svg');
  height: rem(15);
  width: rem(18);
  margin-left: rem(10);

  @supports (mask-image: url()) {
    background-image: none;
    mask-image: url('../../images/icon-twitter.svg');
  }
}

// Widget Area Overrides
.sidebar--home {

  .sidebar__widgets {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-between;

    @include breakpoints(small, large) {
      flex-direction: row;
      flex-wrap: nowrap;
    }
  }

  @include breakpoints(x-small, medium) {
    margin-top: rem(80);
  }

  .widget {
    flex-basis: 100%;
    flex-grow: 0;
    margin-bottom: rem(20);

    @include breakpoints(small, large) {
      flex-basis: 45%;
    }
  }

  .widget--events,
  .widget--subscribe {
    order: -1;

    @include breakpoints(small, large) {
      order: 2;
    }
  }

  .widget--slack,
  .widget--twitter {
    order: 1;
  }

  .widget--subscribe,
  .widget--twitter {
    margin-right: 0;
  }
}

// Newsletter Widget
.subscribe__form {
  position: relative;

  // Highlight form on input or submit focus
  &:focus-within {
    /* stylelint-disable selector-max-specificity */
    .subscribe__email {
      border-color: $poppy;
      outline: none;
    }

    .subscribe__submit {
      background-color: $poppy;
      border-color: $poppy;
    }
    /* stylelint-enable */
  }
}

.subscribe__email {
  border: $border-dark;
  font-size: 1rem;
  height: rem(36);
  padding: 0 rem(48) 0 rem(12);
  width: 100%;

  // Highlight on input field focus only, fallback for IE
  &:focus {
    border-color: $poppy;
    outline: none;

    /* stylelint-disable selector-max-specificity */
    + .subscribe__submit {
      background-color: $poppy;
      border-color: $poppy;
    }
    /* stylelint-enable */
  }

  &::placeholder {
    color: color(gray-very-light);
    opacity: 1;
    font-weight: 300;
  }
}

.subscribe__submit {
  @include text-replace(rem(36), rem(36));
  background: $steel url('../../images/newsletter-submit.svg') center
    no-repeat;
  background-size: rem(9) auto;
  border-radius: 0;
  border: 1px solid $steel;
  color: $white;
  display: block;
  padding: 0;
  position: absolute;
  right: 0;
  top: 0;

  &:focus {
    outline: none;
  }
}

// Links Widget
.widget__links-link {
  color: $steel;

  img {
    margin-bottom: spacer(1);
    margin-top: spacer(1);
  }
}

// Marketo widget
//
// NOTE: Marketo injects very aggressive styles and inline css
// with long chains of specificity and many !important flags,
// which requires that we do the same in order to override.

/* stylelint-disable selector-max-specificity, declaration-no-important */
.sidebar__widgets .mktoFormRow {
  width: 100%;
}

.mktoForm {
  display: flex;

  .mktoFormRow {

    .mktoOffset {
      height: 0;
    }

    .mktoLabel {
      line-height: 0;
      margin: 0;
      padding: 0;
    }

    @include breakpoints(x-small, large) {
      width: 100%;
    }

    .mktoFieldWrap {

      .mktoEmailField {
        border: 1px solid #d1d1d1;
        border-right: 0;
        padding: rem(5);

        @include breakpoints(x-small, large) {
          padding: rem(11.2) rem(7);
        }
      }
    }
  }

  .mktoButtonRow {
    margin-left: 0 !important;
    margin-top: 0 !important;

    @include breakpoints(x-small, large) {
      display: inline !important;
      float: right;
      width: auto;
    }

    .mktoButton {
      @include breakpoints(marketomobilebkpt, large) {
        padding: 0.7rem 0.9rem !important;
      }

      @include breakpoints(x-small, marketomobilebkpt) {
        padding: 0.8rem !important;
      }
    }
  }
}

.subscribe-input__btn {
  height: 10px;
  max-width: 9px;
  padding: 0 !important;
  width: 9px;
}
/* stylelint-enable selector-max-specificity, declaration-no-important */

// Twitter Widget
.twitter-module {
  max-height: rem(500);
  overflow: auto;
}

.widget-ad-wrap {
  padding-right: 30px;

  a.widget-ad__link {
    position: relative;
    display: block;
  }

  img {
    display: block;
    margin: 0 auto rem(20);
    max-width: 100%;
    width: 100%;
  }
}
