$font-path: '/wp-content/themes/a16z-2015/client/fonts';

@font-face {
  font-family: 'icomoon';
  src: url('#{$font-path}/icomoon/icomoon.eot?qbxk8l');
  src: url('#{$font-path}/icomoon/icomoon.eot?qbxk8l#iefix') format('embedded-opentype'), url('#{$font-path}/icomoon/icomoon.ttf?i2yv2h3r123') format('truetype'), url('#{$font-path}/icomoon/icomoon.woff?i2yvh3234r1') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
  font-family: 'icomoon';
  speak: none;
  font-style: normal;
  font-weight: 400;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

