/*
 * Site
 */

$width-primary-content: calc((824 / 1180) * 100%);
$width-secondary-content: calc((324 / 1180) * 100%);

$width-mobile-nav: rem(320);

html {
	max-width: 100vw;
	overflow-x: hidden;
	// font-size: calc((#{$base-size}/16) * 100%);
	font-size: 16px;
	height: 100%;
}

body {
	font: $font-body;
	color: $steel;
	background: color(gray-very-light);
	height: 100%;

	@include breakpoints(large) {
		padding: 0 2%;
		background: transparent;
	}

	&.toggle__mobile-nav--active {
		@include breakpoints($to:large) {
			overflow: hidden;
		}
	}
}

.site {
	background: $white;
	display: flex;
	flex-direction: column;
	margin: 0 auto;
	max-width: $site-max-width;
	min-height: 100vh;
	transition: transform 0.5s;
	overflow: hidden;

	@include breakpoints(large) {
		display: block;
		min-height: 0;
	}
}

.site-content {
	margin-bottom: spacer(6);
	display: flex;
	flex-direction: column;
	padding: rem(30) rem(20) 0;

	@include breakpoints(medium) {
		padding: 0 rem(64);
	}

	@include breakpoints(large) {
		flex-direction: row;
		padding: 0;
	}
}

.site-content__primary,
.archive-content--author {
	width: 100%;
  background: $white;

	@include breakpoints(medium, large) {
		margin-bottom: spacer(8);
	}

	.sidebar--right &,
	.sidebar--left & {
		@include breakpoints(large) {
			width: $width-primary-content;
		}
	}

	.sidebar--right & {
		@include breakpoints(large) {
			margin-right: spacer(2);
			padding-right: spacer(2);
			border-right: $border-medium;
		}
	}

	.sidebar--left & {
		margin-left: spacer(2);
		padding-left: spacer(2);
		border-left: $border-medium;
		order: 2;
	}
}

.site-content__secondary,
.archive-content__secondary.sidebar--author {
	@include breakpoints(large) {
		width: $width-secondary-content;
	}
}

// Import TinyMCE WYSIWYG rich text styles into global wp-content class
.wp-content,
.wp-editor-container {
	@import 'wp-content';

	@include vertical-rhythym(spacer(2));

	// Global wp-content overrides
	a {
		color: $steel;
		text-decoration: underline;

    overflow-wrap: break-word;
    word-wrap: break-word;
    -ms-word-break: break-all;
    word-break: break-all;
    word-break: break-word;
    -ms-hyphens: auto;
    -moz-hyphens: auto;
    -webkit-hyphens: auto;
    hyphens: auto;

		&:hover {
			color: $poppy;
		}
	}

  h2 {

    span {
      // stylelint-disable-next-line declaration-no-important
      color: $poppy !important;
    }
  }

  .table-wrap {
    overflow-x: auto;
  }

  table {
    font-size: 12px;
    min-width: 800px;
    display: table;
    margin-bottom: 0;

    th,
    td {
      border: none;
    }

    thead {
      td {
        font-weight: 600;
      }
    }

    tbody {
      td {
        border: solid #c9c9c6;
        border-width: 1px 0;
      }
    }

    // stylelint-disable selector-max-specificity
    &.table--auto {
      td {
        // stylelint-disable-next-line declaration-no-important
        width: auto !important;
      }
    }

    &.table--style-general,
    &.table--style-bio,
    &.table--style-enterprise,
    &.table--style-consumer,
    &.table--style-crypto,
    &.table--style-fintech {
      thead {
        td {
          color: $white;
        }
      }

      tbody {
        td {
          border: 0 none;
        }

        tr {
          &:nth-of-type(even) {
            background: $gray;
          }
        }
      }
    }

    &.table--style-general {
      border: 1px solid $poppy;

      thead {
        td {
          background: $poppy;
        }
      }
    }

    &.table--style-bio {
      border: 1px solid $bayside;

      thead {
        td {
          background: $bayside;
        }
      }
    }

    &.table--style-enterprise {
      border: 1px solid $deep_ocean;

      thead {
        td {
          background: $deep_ocean;
        }
      }
    }

    &.table--style-consumer {
      border: 1px solid $avocado;

      thead {
        td {
          background: $avocado;
        }
      }
    }

    &.table--style-crypto {
      border: 1px solid $grapevine;

      thead {
        td {
          background: $grapevine;
        }
      }
    }

    &.table--style-fintech {
      border: 1px solid $marina;

      thead {
        td {
          background: $marina;
        }
      }
    }
    // stylelint-enable selector-max-specificity
  }
}

// stylelint-disable-next-line selector-max-specificity
#wpstats {
	display: none;
}

.screen-reader-text {
	@include screen-reader-text;
}

.mejs-mediaelement mediaelementwrapper {
	display: block;
	width: 100%;
}

.bctt-click-to-tweet {
	color: $poppy;
	border: none;
	border-radius: 0;
	padding: rem(40) 0.625rem;
	margin: 0 0 rem(32);
	border: solid #d1d1d1;
	border-width: 1px 0;
	position: relative;
	text-align: center;

	@include breakpoints(medium) {
		padding-left: rem(130);
		text-align: left;
	}

	.bctt-ctt-text {
		margin-bottom: rem(10);
		display: block;
		z-index: 1;

		&::before {
			content: '';
			width: rem(81);
			height: rem(52);
			background: #d1d1d1 url('../../images/icon-quote.svg') no-repeat center;
			transform: scale(-1,-1);
			display: block;
			margin: 0 auto rem(20);

			@include breakpoints(medium) {
				position: absolute;
				left: rem(19);
				top: rem(21);
				margin-bottom: 0;
			}

			@supports (mask-image: url()) {
				mask-position: center;
				mask-repeat: no-repeat;
				mask-size: 100% auto;
				background-image: none;
				mask-image: url('../../images/icon-quote.svg');
			}
		}

		a {
			padding: 0;
			margin: 0;
			flex: 1;
			display: block;
			// stylelint-disable-next-line declaration-no-important
			color: $poppy !important;
			// stylelint-disable-next-line declaration-no-important
			font: 400 rem(18)/1.33 font-family(sans-serif) !important;

			@include breakpoints(large) {
				// stylelint-disable-next-line declaration-no-important
				font-size: rem(21) !important;
			}
		}
	}

	a.bctt-ctt-btn {
		float: none;
		display: inline-block;
		vertical-align: top;
		// stylelint-disable-next-line declaration-no-important
		color: $bayside !important;
		// stylelint-disable-next-line declaration-no-important
		font-family: font-family(sans-serif) !important;
		background: none;

		// stylelint-disable-next-line selector-max-specificity
		&::after {
			content: '';
			width: 18px;
			height: 19px;
			position: absolute;
			right: 0;
			top: 6px;
			background: url('../../images/icon-twitter.svg') no-repeat center/ 100% auto;

			@supports (mask-image: url()) {
				background: $bayside;
				mask-position: center;
				mask-repeat: no-repeat;
				mask-size: 100% auto;
				mask-image: url('../../images/icon-twitter.svg');
			}
		}
	}
}

// stylelint-disable
.site-content {
  .wp-content {
    .embed-youtube {
      position: relative;
      display: block;
      padding-bottom: 56.25%;
      width: 100%;

      iframe.youtube-player {
        position: absolute;
        width: 100%;
        height: 100% !important;
      }
    }
  }
}

.hide-on-mobile {
  @include breakpoints(none, s-medium) {
    display: none !important;
  }
}

.show-on-mobile {
  @include breakpoints(s-medium) {
    display: none !important;
  }
}

.form-wrap .mktoForm .mktoGutter {
  display: none !important;
}

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 11px 8px;
  font-size: 16px;
  line-height: 1.2;
  font-weight: 600;
  transition: all 0.3s linear;
  color: $white;
  text-decoration: none;
  text-align: center;

  @include breakpoints(s-medium) {
    padding: 11px 21px;
  }

  &.btn-orange {
    transition: background 0.3s linear;
    background: $poppy;

    &:hover {
      background: $poppy;
    }
  }

  &.btn-black {
    transition: background 0.3s linear;
    background: $black;

    &:hover {
      background: #111111;
    }
  }

  &.btn-dark-blue {
    transition: background 0.3s linear;
    background: $deep_ocean;

    &:hover {
      background: darken($deep_ocean, 10);
    }
  }

  &.btn-future-blue {
    transition: background 0.3s linear;
    background: $future_blue;

    &:hover {
      background: darken($future_blue, 10);
    }
  }

  &.btn-white {
    transition: background 0.3s linear;
    background: $white;
    color: $future_blue;

    &:hover {
      background: darken($white, 10);
    }
  }
}
// stylelint-enable
