/*
 * Search Form Module Styles
 */

.search-form {
  align-items: center;
  display: flex;
  font-size: rem(18);
  height: rem(40);
  position: relative;
  margin-bottom: 0;

	@include breakpoints(large) {
		height: rem(48);
	}
}

.search-form__input {
  appearance: none;
  border-bottom: 2px solid color(gray-light);
  border-radius: 0;
  border: none;
  font-size: inherit;
  height: 100%;
  line-height: 1;
  padding: 0 2.5em 0 0.5em;
  width: 100%;

	&:focus {
		border-bottom: 2px solid $poppy;
		outline: none;

    // stylelint-disable-next-line selector-max-specificity
		+ .search-form__submit svg {
			fill: $poppy;
		}
	}

	@include breakpoints(large) {
		border: $border-medium;
		padding: 0 2.5em 0 1em;

		&:focus {
			border: 1px solid $poppy;
		}
	}
}

.search-form__submit {
	@include button-reset;
	align-items: center;
	display: flex;
	height: 100%;
	position: absolute;
	right: 0;
	top: 0;
	width: rem(24);

	&:hover {
		cursor: pointer;
	}

	@include breakpoints(none, large) {

		.search-form--site-header & {
			border: 0 none;
			clip: rect(0, 0, 0, 0);
			height: 1px;
			margin: -1px;
			padding: 0;
			position: absolute;
			width: 1px;
			visibility: hidden;
		}
	}

	@include breakpoints(large) {
		height: rem(24);
		right: spacer(1, em);
		top: 50%;
		transform: translateY(-50%);
		width: rem(18);
	}

	svg {
		position: absolute;
		top: 6px;
		width: 100%;

		@include breakpoints(large) {
			position: static;
			top: auto;
		}
	}
}
