/*
 * Site Header
 */

.skip-link {
    @include link-hover;
    background-color: $white;
    border: $border-light;
    color: color(gray);
    display: block;
    left: spacer(1);
    line-height: 1;
    padding: spacer(1);
    position: absolute;
    top: spacer(-1000);

    &:focus {
        top: spacer(1);
    }
}

.site-header {
    border-bottom: $border-light;
    display: flex;
    padding: rem(16) rem(18) rem(14);
    position: relative;

    @include breakpoints(medium) {
        padding: rem(32) rem(32) rem(16);
        margin-bottom: spacer(4);
    }

    @include breakpoints(large) {
        border: none;
        flex-wrap: wrap;
        padding: rem(60) rem(0) rem(0);
    }

    svg {
        fill: $steel;
    }
}

.site-header__branding {
    flex: 1 1 auto;
    text-align: center;
    margin-bottom: 0;

    @include breakpoints(large) {
        text-align: left;
        display: flex;
        justify-content: flex-start;
        align-items: flex-end;
    }
}

.site-header__site-title {
    @include screen-reader-text();
}

.site-header__site-logo {
    align-content: flex-end;
    // border: 2px solid magenta;
    display: flex;
    flex-flow: row nowrap;
    margin: 0 auto;
    max-width: rem(210);
    width: 90%;

    &:hover {
        opacity: 0.7;
    }

    @include breakpoints(medium) {
        max-width: rem(358);
        margin-bottom: rem(8);
    }

    @include breakpoints(large) {
        width: rem(364);
        margin: rem(0 16 0 0);
    }

    svg {
        display: block;
        width: 100%;
    }
}

.site-header__site-subtitle {
    color: color(gray-light);
    font: italic rem(10)/1 font-family(sans-serif);

    @include breakpoints(medium) {
        font-size: rem(14);
    }

    @include breakpoints(large) {
        font-size: rem(12);
    }
}

.site-header__mobile-toggle {
    @include button-reset(white);
    color: $steel;
    display: block;
    height: rem(24);
    width: rem(24);

    &:focus {
        outline: none;
    }

    // stylelint-disable selector-max-specificity
    svg[data-name='icon-close'] {
        display: none;
    }

    &[aria-expanded='true'] {

        svg[data-name='icon-menu'] {
            display: none;
        }

        svg[data-name='icon-close'] {
            display: block;
        }
    }

    // stylelint-enable selector-max-specificity

    @include breakpoints(none, large) {
        position: relative;
        z-index: z-index(first);
    }

    @include breakpoints(medium) {
        height: rem(36);
        width: rem(36);
    }

    @include breakpoints(large) {
        display: none;
    }
}

.site-header__mobile-close {
    background: none;
    border: none;
    outline: none;
    appearance: none;
    width: rem(22);
    height: rem(22);
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 0;

    path {
        fill: white;
    }
}

.site-header__search {
    margin-bottom: 0;
}

.site-header__menu,
.site-header__search {
    flex: 0 0 auto;
    max-width: rem(24);

    @include breakpoints(medium) {
        max-width: rem(36);
    }

    @include breakpoints(large) {
        max-width: none;
    }
}

.site-header__menu {
    order: -1;

    @include breakpoints(large) {
        order: 1;
        width: 100%;
    }
}

.site-header__menu-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    background: $poppy;
    width: 100%;
    padding: 57px 21px 21px;
    font-size: rem(24);

    @include breakpoints($to: large) {
        overflow-y: auto;
        z-index: 1;
        transition: transform 0.25s ease;
        transform: translateX(-100%);
        -webkit-overflow-scrolling: touch;
    }

    .toggle__mobile-nav--active & {
        transform: translateX(0);
    }

    li {
        position: relative;
        margin-bottom: 2px;
    }

    a {
        color: white;
        font-weight: 700;
        text-decoration: none;

        // stylelint-disable selector-max-specificity
        &[href*="twitter.com"],
        &[href*="soundcloud.com"],
        &[href*="simplecast.com"],
        &[href*="youtube.com"] {
            text-indent: -9999px;
            overflow: hidden;
            background-size: 100% auto;
            background-repeat: no-repeat;
            display: block;
            opacity: 0.5;
        }

        &[href*="twitter.com"] {
            width: 19px;
            height: 16px;
            background-image: url('../../images/icon-twitter-white.svg');
        }

        &[href*="soundcloud.com"] {
            width: 34px;
            height: 15px;
            background-image: url('../../images/icon-soundcloud-white.svg');
        }

        &[href*="simplecast.com"] {
            width: 18px;
            height: 18px;
            background-image: url('../../images/icon-simplecast-white.svg');
        }

        &[href*="youtube.com"] {
            width: 24px;
            height: 17px;
            background-image: url('../../images/icon-youtube-white.svg');
        }

        // stylelint-enable selector-max-specificity

    }

    @include breakpoints(large) {
        background: transparent;
        border-top: $border-medium;
        margin-top: spacer(1);
        padding: rem(10 0 0);
        position: static;
        font-size: rem(18);

        /*
         * Allow site admins to add an unlimited number of
         * menus to this area for mobile display, but only
         * show the site-header menu on desktop.
         */

        > *:not(.menu-site-header-nav) {
            display: none;
        }

        .menu {
            display: flex;
            justify-content: flex-end;

            // stylelint-disable-next-line selector-max-specificity
            > li:hover > a,
            > li > a:hover {
                color: $poppy;
            }
        }

        li {
            border: none;
            margin-left: rem(50);
            margin-bottom: 0;
            padding: 0;

            &:first-child {
                margin-left: 0;
            }

            &:hover {
                // stylelint-disable-next-line selector-max-specificity
                .sub-menu {
                    opacity: 1;
                    visibility: visible;
                    transform: translateX(-50%) translateY(0) scale(1);
                }
            }
        }

        a {
            color: $steel;
            font-weight: 400;
        }
    }
}

.site-header__search-wrapper {
    margin: rem(10 18 14);
    position: absolute;
    top: 0;
    right: 0;
    width: 0;
    overflow: hidden;

    @include breakpoints(none, large) {
        transition: width 1s;
    }

    @include breakpoints(none, medium) {
        margin: rem(10 0 14 18);
        right: calc(36px + 20px);

        &.toggle__mobile-search--active {
            width: 70vw;
        }
    }

    @include breakpoints(medium, large) {
        margin: rem(25 0 14 18);
        right: calc(100px);

        &.toggle__mobile-search--active {
            width: calc(100% - 200px);
        }
    }

    @include breakpoints(large) {
        transform: none;
        position: relative;
        width: 360px;
        transition: none;
        margin: 0;
    }
}

.sub-menu {
    font-size: 18px;
    margin-top: -2px;
    padding-left: 17px;

    @include breakpoints(large) {
        position: absolute;
        top: 100%;
        left: 50%;
        background: rgba(#dfdfdf, 0.95);
        font-size: 19px;
        line-height: 1.3;
        margin-top: 15px;
        padding-left: 0;
        text-align: center;
        min-width: 170px;
        opacity: 0;
        visibility: hidden;
        will-change: transform, opacity, visibility;
        transform: translate(-50%, -25px) scale(0.93);
        box-shadow: 0 2px 4px 0.7px rgba($black, 0.12);
        transition: opacity 0.3s ease, visibility 0.3s ease, transform 0.3s ease;
    }

    &::after {
        content: '';
        position: absolute;
        bottom: 100%;
        left: 0;
        right: 0;
        height: 30px;
    }

    &::before {
        position: absolute;
        left: 50%;
        bottom: 100%;
        transform: translateX(-50%);
        width: 0;
        height: 0;
        border-left: 17px solid transparent;
        border-right: 17px solid transparent;
        border-bottom: 11px solid rgba(#dfdfdf, 0.95);
        will-change: transform;
        z-index: -1;

        @include breakpoints(large) {
            content: '';
        }
    }

    li {
        margin: 0;

        @include breakpoints($to: large) {
            margin-bottom: 2px;
        }
    }

    a {
        font-weight: 400;

        @include breakpoints(large) {
            display: block;
            padding: 7px 10px;
            // stylelint-disable-next-line declaration-no-important
            color: $steel !important;
        }

        &:hover {
            @include breakpoints(large) {
                background: rgba(#ccc, 0.75);
            }
        }
    }
}

.menu-social-media-header {
    margin: rem(37 0 40);

    ul {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
    }

    li {
        margin-right: rem(16);
    }
}

.search-form.search-form--mobile {
    margin-bottom: rem(17);
    height: rem(27);
    padding-right: rem(10);

    @include breakpoints(large) {
        display: none;
    }

    input {
        background: none;
        border-bottom: 1px solid $poppy;
        color: $poppy;
        font-size: rem(18);
        font-style: oblique;
        padding: rem(0 5 0 0);
        line-height: 1;

        // stylelint-disable-next-line selector-max-specificity
        &::placeholder {
            color: $poppy;
            opacity: 1;
        }

        // stylelint-disable-next-line selector-max-specificity
        &:focus + button svg {
            fill: $poppy;
        }
    }

    button {
        width: rem(18);
        right: -5px;
        top: 4px;
    }

    svg {
        fill: $poppy;
    }
}

// stylelint-disable selector-max-specificity

.header-new {
    background: $white;
    position: relative;

    .single-a16z_event &,
    .page-template-page_template-vertical-landing &,
    .page-template-page_template-summit-event &,
    .single-a16z_book.full-bleed-banner & {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        background: none;
    }

    .single-a16z_book & {
        // stylelint-disable-next-line declaration-no-important
        background: transparent !important;
    }

    .page-template-page-templatespage_template-home-php & {
        display: block;
    }

    &.has-ab-test {
        min-height: 336px;
    }

    .bg-header {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 1;

        .single-a16z_book & {
            // stylelint-disable-next-line declaration-no-important
            background: transparent !important;
        }
    }

    > .container {
        position: relative;
        z-index: 2;

        .single-a16z_book & {
            max-width: $site-max-width;

            @include breakpoints(s-medium) {
                padding-left: rem(32);
                padding-right: rem(32);
            }
        }
    }

    .header-holder {
        padding: rem(11 0);
        position: relative;

        @include breakpoints(s-medium) {
            display: flex;
            align-items: center;
            padding: rem(32px 0);
            justify-content: space-between;
        }

        @include breakpoints(sm-large) {
            align-items: flex-end;
        }

        .single-a16z_book & {
            @include breakpoints(s-medium) {
                padding-left: 2%;
                padding-right: 2%;
            }
        }

        .search {
            @include breakpoints(s-medium) {
                text-align: right;
                position: absolute;
                right: 0;
                top: 53px;
                z-index: 9999;
                width: 50px;
            }

            @include breakpoints(s-large) {
                width: 61px;
            }

            @include breakpoints(sm-large) {
                top: 59px;
            }

            .search-opener-wrap {
                width: 40px;
                position: absolute;
                right: 0;
                top: 27px;
                text-align: right;
                z-index: 9999;

                @include breakpoints(s-medium) {
                    position: static;
                    width: auto;
                }
            }

            .icon-search {
                transition: color 0.3s linear;
                position: relative;
                width: 20px;
                height: 20px;
                display: inline-block;
                vertical-align: middle;
                color: $steel;
                text-decoration: none;

                &:hover {
                    color: $poppy;
                }

                .page-template-page_template-summit-landing &,
                .page-template-page_template-crypto-policy &,
                .navbar-color-light & {
                    color: $white;

                    &:hover {
                        color: $steel;
                    }
                }

                .page-template-page_template-social-strikes-back-lp & {
                    color: $black;

                    &:hover {
                        color: lighten($black, 10%);
                    }
                }

                &::before {
                    content: "\e90b";
                    font: 20px/1 "icomoon";
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                }
            }

            &.style-1 {
                @include breakpoints(s-medium) {
                    top: 52px;
                }

                @include breakpoints(sm-large) {
                    top: 46px;
                }

                .search-box {
                    @include breakpoints(s-medium) {
                        margin-top: 11px;
                    }
                }
            }
        }
    }

    .tagline {
        display: block;
        font-size: rem(15);
        line-height: 1;
        font-weight: 300;
        color: $concrete;
        text-align: center;

        .page-template-page_template-summit-landing,
        .page-template-page_template-crypto-policy {
            color: $white;
        }

        .post-type-archive-a16z_demo & {
            color: $steel;
        }

        @include breakpoints(none, s-medium) {
            z-index: 9999;
            position: relative;
        }

        @include breakpoints(sm-large) {
            font-size: rem(17);
            text-align: left;
            margin-bottom: -2px;
        }
    }

    .search-box {
        transition: opacity 0.3s linear, visibility 0.3s linear;
        background: $white;
        width: 100%;
        height: 42px;
        position: absolute;
        padding: 0 38px;
        top: 100%;
        right: 0;
        left: 0;
        overflow: hidden;
        opacity: 0;
        visibility: hidden;
        z-index: 9999;
        margin-top: -25px;
        border: 1px solid #d0d0ce;

        @include breakpoints(s-medium) {
            width: 390px;
            left: auto;
            margin-top: 6px;
        }

        @include breakpoints(s-large) {
            width: 420px;
        }

        .search-active & {
            opacity: 1;
            visibility: visible;
        }

        button {
            display: block;
            position: absolute;
            left: 0;
            top: 0;
            bottom: 0;
            width: 38px;
            cursor: pointer;
            border: 0 none;
            background: $white;

            &::before {
                content: "\e90b";
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                color: $poppy;
                font: 18px/1 "icomoon";
            }
        }

        input[type="search"] {
            border: none;
            outline: none;
            height: 42px;
            font-size: 16px;
            line-height: 1.2;
            width: 100%;
            padding: 0;
        }

        .close {
            position: absolute;
            right: 10px;
            top: 50%;
            transform: translateY(-50%);
            width: 20px;
            height: 20px;

            &:hover {

                &::before,
                &::after {
                    background: $poppy;
                }
            }

            &::before,
            &::after {
                content: "";
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%) rotate(45deg);
                width: 2px;
                height: 20px;
                background: $steel;
                transition: background 0.3s linear;
            }

            &::after {
                transform: translate(-50%, -50%) rotate(-45deg);
            }
        }
    }

    .logo-wrap {
        @include breakpoints(sm-large) {
            display: flex;
            align-items: flex-end;
        }

        .logo {
            @include breakpoints(sm-large) {
                margin-right: 15px;
            }
        }
    }
}

#nav {
    transition: opacity 0.3s linear, visibility 0.3s linear;
    position: absolute;
    top: 0;
    left: -16px;
    right: -16px;
    background: $white;
    z-index: 9997;
    padding-top: rem(48);
    opacity: 0;
    visibility: hidden;

    .nav-active & {
        opacity: 1;
        visibility: visible;
    }

    @include breakpoints(none, s-medium) {
        border-bottom: 1px solid $fog;
        padding-top: rem(94);
    }

    @include breakpoints(s-medium) {
        display: block;
        position: static;
        padding: rem(0 32 0 0);
        background: transparent;
        opacity: 1;
        visibility: visible;
        margin-top: -6px;
        margin-left: auto;
    }

    @include breakpoints(s-large) {
        padding-right: rem(63);
    }

    > ul {
        margin: 0;
        padding: 0;
        list-style: none;

        @include breakpoints(s-medium) {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            margin: rem(0 -12);
        }

        @include breakpoints(s-large) {
            margin: rem(0 -25);
        }

        > li {
            border-top: 1px solid $fog;

            @include breakpoints(s-medium) {
                padding: rem(0 8);
                border: none;
            }

            @include breakpoints(850px) {
                padding: rem(0 12);
            }

            @include breakpoints(s-large) {
                padding: rem(0 25);
            }

            &.has-drop {

				> a {
					@include breakpoints(s-medium) {
						// stylelint-disable-next-line declaration-no-important
						padding: rem(0 20 0 0) !important;
					}

                    &::after {
                        transition: transform 0.3s linear;
                        content: "\e90d";
                        font: 12px/1 "icomoon";
                        position: absolute;
                        right: 18px;
                        top: 50%;
                        transform: translateY(-50%);

                        @include breakpoints(s-medium) {
                            right: 0;
                            font-size: 8px;
                        }
                    }
                }

                &:hover {
                    @include breakpoints(s-medium) {
                        > a {
                            color: $poppy;

                            &::after {
                                transform: translateY(-50%) rotate(180deg);
                            }
                        }

                        .nav-drop {
                            opacity: 1;
                            visibility: visible;

                            @media only screen and (max-width: 767px) {
                                display: block;
                            }
                        }
                    }
                }

                &.nav-drop--opened {
                    @include breakpoints(none, s-medium) {
                        > a {
                            color: $poppy;

                            &::after {
                                transform: translateY(-50%) rotate(180deg);
                            }
                        }

                        .nav-drop {
                            opacity: 1;
                            visibility: visible;

                            @media only screen and (max-width: 767px) {
                                display: block;
                            }
                        }
                    }
                }
            }

            > a:not(.btn) {
                display: block;
                color: $steel;
                text-decoration: none;
                transition: color 0.3s linear;
                position: relative;
                font-weight: 700;
                z-index: 101;
                padding: rem(18 16);
                font-size: rem(22);
                line-height: 1.23;

                .page-template-page_template-summit-landing,
                .page-template-page_template-crypto-policy {
                    color: $white;
                }

                @include breakpoints(s-medium) {
                    font-size: rem(16);
                    line-height: 1;
                    padding: 0;
                }

                &:hover {
                    @include breakpoints(s-medium) {
                        color: $poppy;

                        .page-template-page_template-summit-landing &,
                        .page-template-page_template-crypto-policy & {
                            color: $steel;
                        }

                        &::after {
                            transform: translateY(-50%) rotate(180deg);
                        }
                    }
                }
            }

            > a.btn {
                color: $white;
                margin: rem(18 16);

                @include breakpoints(s-medium) {
                    margin: 0;
                }
            }
        }
    }
}

.page-template-page_template-crypto-policy,
.navbar-color-light {
    #nav {
        > ul {
            > li {
                > a:not(.btn) {
                    @include breakpoints(s-medium) {
                        color: $white;

                        &:hover {
                            color: $poppy;
                        }
                    }
                }
            }
        }
    }

    &:not(.nav-active) {
        .nav-opener span,
        .nav-opener::before,
        .nav-opener::after {
            background: $white;
        }

        .header-holder .search .icon-search {
            color: $white;
        }

        svg path {
            fill: $white;
        }

        .logo {
            color: $white;
        }

        .tagline {
            color: $white;
        }
    }
}

.page-template-page_template-social-strikes-back-lp {
    #nav {
        > ul {
            > li {
                > a {
                    @include breakpoints(s-medium) {
                        color: $black;

                        &:hover {
                            color: lighten($black, 10%);
                        }
                    }
                }
            }
        }
    }

    &:not(.nav-active) {
        .nav-opener span,
        .nav-opener::before,
        .nav-opener::after {
            background: $black;
        }

        .header-holder .search .icon-search {
            color: $black;
        }

        .logo-wrap {
            svg path {
                fill: $black;
            }
        }

        .logo {
            color: #40474f;
        }

        .tagline {
            color: $black;
        }
    }
}

.nav-drop {
    @include breakpoints(s-medium) {
        transition: opacity 0.3s linear, visibility 0.3s linear;
        margin-top: -50px;
        padding: 30px 0 0;
        position: absolute;
        top: 100%;
        width: 100%;
        max-width: 500px;
        right: 0;
        opacity: 0;
        visibility: hidden;

        .is-single & {
            max-width: 250px;
            right: auto;
        }
    }

    @media only screen and (max-width: 767px) {
        display: none;
    }

    .holder {
        z-index: 100;
        padding: rem(0 16);

        @include breakpoints(s-medium) {
            position: relative;
            padding: rem(30 32);
            background: #fdfdfc;
        }

        @include breakpoints(s-1350) {
            margin: rem(0 -30);
        }
    }

    .two-cols {
        @include breakpoints(s-medium) {
            display: flex;
            flex-wrap: wrap;
        }

        .col {
            @include breakpoints(s-medium) {
                width: 50%;

                .is-single & {
                    width: 100%;
                }
            }

            + .col {
                margin-top: rem(5);

                @include breakpoints(s-medium) {
                    margin-top: 0;
                }
            }
        }
    }

    .title {
        font-weight: 400;
        color: $poppy;
        font-size: rem(16);
        line-height: 1.2;
        display: block;
        margin-bottom: rem(10);
        text-decoration: none;
        padding-bottom: rem(16);

        @include breakpoints(s-medium) {
            font-size: rem(20);
            padding-bottom: rem(3);
        }
    }

    ul {
        margin: 0;
        padding: 0;
        list-style: none;
        display: flex;
        flex-wrap: wrap;
        font-size: rem(14);
        font-weight: 300;

        @include breakpoints(s-medium) {
            font-size: rem(16);
        }

        li {
            width: 100%;
            margin-bottom: rem(23);

            @include breakpoints(s-medium) {
                margin-bottom: rem(5);
            }

            a {
                transition: color 0.3s linear;
                color: $steel;
                text-decoration: none;

                &:hover {
                    color: $poppy;
                }
            }
        }
    }
}

.nav-opener {
    width: 20px;
    height: 18px;
    position: absolute;
    left: 0;
    top: 27px;
    z-index: 9999;

    @include breakpoints(s-medium) {
        display: none;
    }

    .nav-active & {
        span {
            opacity: 0;
        }

        &::before,
        &::after {
            transform: rotate(45deg);
            top: 50%;
            left: 0;
            right: 0;
        }

        &::after {
            transform: rotate(-45deg);
        }
    }

    span,
    &::before,
    &::after {
        background: $steel;
        position: absolute;
        top: 50%;
        left: 0;
        right: 0;
        height: 2px;
        margin-top: -1px;
        transition: all 0.3s linear;
    }

    &::before,
    &::after {
        content: '';
        top: 2px;
    }

    &::after {
        top: 17px;
    }
}

.signup-row {
    padding: rem(31 0 32);
    font-size: rem(14);
    line-height: 1.2;
    color: $steel;

    @include breakpoints(s-medium) {
        padding: rem(18 0 72);
        font-size: rem(16);
    }

    h2 {
        font-weight: 300;
        margin-bottom: rem(7);
        font-size: rem(28);
        line-height: 1.21;

        @include breakpoints(s-medium) {
            font-size: rem(36);
            line-height: 1.22;
        }
    }

    p {
        margin-bottom: rem(8);
        font-weight: 300;

        @include breakpoints(s-medium) {
            margin-bottom: rem(16);
        }
    }

    .form-wrap {
        background: $white;

        @include breakpoints(s-medium) {
            max-width: rem(463);
        }
    }
}

.signup-form {
    padding-right: 125px;
    position: relative;

    input[type="email"] {
        width: 100%;
        border: none;
        outline: none;
        background: none;
        height: 42px;
        font-size: rem(14);
        line-height: 1.2;
        color: $steel;
        box-shadow: none;
        padding: rem(10);
        font-weight: 300;
        -webkit-border-radius: 0;

        @include breakpoints(s-medium) {
            font-size: rem(16);
        }
    }

    input[type="submit"] {
        transition: background 0.3s linear;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        width: 125px;
        cursor: pointer;
        border: none;
        background: $poppy;
        color: $white;
        padding: rem(0 13);
        font-size: rem(16);
        line-height: 1.5;
        font-weight: 600;
        -webkit-appearance: none;
        -webkit-border-radius: 0;
        cursor: pointer;

        &:hover {
            background: $poppy;
        }
    }
}

