.container {
    max-width: rem(1350);
    margin: 0 auto;
    padding-left: rem(16);
    padding-right: rem(16);

    @include breakpoints(s-medium) {
        padding-left: rem(20);
        padding-right: rem(20);
    }
}

.content-new {
    background: $white;
    margin: 0 auto;
    max-width: $site-max-width;
    min-height: 100vh;
    padding-top: spacer(4);
    padding-bottom: 1px;

    @include breakpoints(large) {
        min-height: 0;
    }

    .single-a16z_book & {
        background: transparent;
        padding-top: 0;
    }
}

.content-new > .container {
    margin-bottom: spacer(6);
    padding: rem(30) rem(20) 0;

    @include breakpoints(medium) {
        padding: 0 rem(64);
    }

    @include breakpoints(large) {
        padding: 2%;
    }

    .single-a16z_book & {
        margin-bottom: 0;
        padding-top: 0;
        padding-bottom: 0;
        max-width: $site-max-width;
    }
}

.logo {
    width: 196px;
    margin: 0 auto 9px;
    display: block;
    position: relative;
    z-index: 9999;
    color: #40474f;

    @include breakpoints(sm-large) {
        width: 210px;
        margin-bottom: 0;
    }

    img {
        vertical-align: middle;
    }

    svg {
        max-width: 100%;
        display: block;
        fill: currentColor;
        transition: color 0.2s ease, fill 0.2s ease;

        @include breakpoints($from: none, $to: sm-large) {
            height: 46px;
        }

        .page-template-page_template-summit-landing,
        .page-template-page_template-crypto-policy {
            color: $white;
        }

        .lr0 {
            fill-rule: evenodd;
            clip-rule: evenodd;
        }
    }
}

.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    border: 0;
}

#bg-header-animate-canvas {
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    width: 100%;
    height: 100%;
}

#bg-header-animate {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    background: #FFFFFF;
    pointer-events: none;

    @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
        left: -100px;
        right: -100px;

        svg {
            width: 100%;
            height: 420px;
            @include breakpoints(1550px) {
                height: 480px;
            }
            @include breakpoints(1690px) {
                height: 534px;
            }
        }
    }

    > svg {
        min-width: 1300px;
    }
}

.info-bar {
    background-color: $future-blue;
    color: $white;
    font-size: rem(16);
    line-height: 1.625;
    padding: 18px 0;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 9999;
    min-height: 62px;
    transition: opacity 0.3s linear, visibility 0.3s linear;

    @include breakpoints(none, small) {
        font-size: rem(14);
    }

    .home & {
        opacity: 0;
        visibility: hidden;

        ~ .header-new {
            margin-top: 0 !important;
        }
    }

    &.fixed {
        opacity: 1;
        visibility: visible;
    }

    ~ .header-new {
        margin-top: 62px;
    }

    .container {
        display: flex;
        align-items: center;
        max-width: 1350px !important;

		@include breakpoints(none, s-large) {
			flex-wrap: wrap;
		}

		@include breakpoints(none, s-medium) {
			justify-content: space-between;
		}
	}

    .left-col {
        padding-right: 56px;
        display: flex;
        align-items: center;

        @include breakpoints(none, small) {
            padding-right: 15px;
            flex: 1;
        }

        @media only screen and (max-width: 374px) {
            flex: none;
            width: 100%;
            padding: 0 0 10px;
        }

        strong {
            padding-left: 16px;
            font-weight: 800;
        }

        .logo-wrap {

            @include breakpoints(none, small) {
                width: 34px;
            }
        }
    }

    .right-col {
        flex: 1;
        display: flex;
        flex-wrap: wrap;

        @include breakpoints(none, s-large) {
            flex: none;
            width: 100%;
            padding-top: 8px;
        }

        @include breakpoints(none, small) {
            width: auto;
            padding-top: 0;
        }
    }

    .text-wrap {
        flex: 1;
        padding-right: 20px;

        @include breakpoints(none, s-large) {
            padding-right: 0;
        }

        @include breakpoints(none, small) {
            display: none;
        }
    }

    .link {

        a {
            text-decoration: none;
            color: $white;
            font-weight: 800;
        }
    }

    p {
        margin: 0;

        + p {
            margin-top: 1em;
        }
    }

    + .page-template-page_template-vertical-landing .header-new {

    }
}

.go-to-link {

    @include breakpoints(none, s-large) {
        position: absolute;
        right: 20px;
        top: 17px;
    }

    @media only screen and (max-width: 374px) {
        position: static;
    }
}

.go-to {
    position: relative;
    font-size: 16px;
    line-height: 1;
    font-weight: 800;
    text-decoration: none;
    color: $white;
    padding-right: 19px;
    transition: color 0.3s linear;

    @include breakpoints(none, small) {
        font-size: 14px;
    }

    &:hover {
        color: $poppy;
    }

    &::after {
        content: "\e921";
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        font: 12px/1 "icomoon";
    }
}

.home {

    #bg-header-animate {
        display: none;
    }
}

