.load-more {
	width: 100%;
	padding-top: spacer(1);
}

.load-more__button {
	appearance: none;
	background: none;
	border: $border-light;
	cursor: pointer;
	display: block;
	font-size: rem(14);
	margin-top: spacer(1);
	padding: rem(12);
	text-transform: capitalize;
	width: 100%;

	&:hover,
	&:focus {
		border-color: $poppy;
		color: $poppy;
	}
}

.load-more__error {
	color: $poppy;
}
