.widget--subscribe + .entry-content__related-posts {
    border-top: 0 none;
}

.widget--subscribe-inline {
  .mktoForm {
    max-height: 42px;
  }
}

.widget--subscribe {
  &.widget--subscribe-theme-bio {
    .input-wrap {
      .mktoForm {
        box-shadow: inset 0 0 0 1px $bayside;

        button[type="submit"] {
          background-color: $bayside !important;

          &:hover {
            background-color: darken($bayside, 10%) !important;
          }
        }
      }
    }
  }

  &.widget--subscribe-theme-enterprise {
    .input-wrap {
      .mktoForm {
        box-shadow: inset 0 0 0 1px $deep_ocean;

        button[type="submit"] {
