.ab-test .entry-content__primary .widget--subscribe {
  @include breakpoints($to:s-medium) {
    border-bottom: 0 none;
  }
}

.widget--subscribe,
.widget--subscribe-inline {
  font-size: 16px;
  line-height: 1.2;

  .entry-content__primary & {
    margin-bottom: rem(0);
    margin-top: rem(40);
    border: solid #d1d1d1;
    border-width: 1px 0;
    padding-top: rem(20);
    padding-bottom: rem(26);

    @include breakpoints(medium) {
      margin-bottom: rem(50);
      margin-top: 0;
    }

    .subscribe__form {
      @include breakpoints(medium) {
        max-width: rem(300);
      }
    }
  }

  .widget__title {
    text-transform: none;
    font-weight: 700;
    font-size: 22px;
    line-height: 1.24;
    margin-bottom: 10px;

    .sidebar__widgets & {
      display: none !important;
    }
  }

  p {
    font-weight: 300;
    margin-bottom: 20px;

    .sidebar__widgets & {
      display: none !important;
    }
  }

  .input-wrap {
    display: flex;
    max-width: 463px;
    width: 100%;
    margin-bottom: 0;

    input[type="email"] {
      height: 42px;
      border: 1px solid #d1d1d1;
      padding: 3px 10px;
      flex: 1;
      width: 100%;
      font-size: 16px;
      outline: none;

      .sidebar__widgets & {
        border-color: $poppy;
      }

      &::placeholder {
        color: $concrete;
        opacity: 1;
        font-weight: 300;
      }
    }

    button {
      padding: 4px 13px;
      color: white;
      background: #d1d1d1;
      width: 127px;
      border: 0;
      outline: none;
      font-size: 16px;
      font-weight: 700;
      cursor: pointer;

      @include breakpoints(none, s-medium) {
        width: 100px;
      }

      .sidebar__widgets & {
        background: $poppy url('data:image/svg+xml;base64,') no-repeat center/14px 14px;
        width: 42px;
      }

      &:hover {
        background-color: $poppy;
      }

      span {
        .sidebar__widgets & {
          display: none;
        }
      }
    }
  }

  .mktoForm {
    background: $white;
    position: relative;
    width: 100% !important;
    margin: 0 !important;
    text-align: left;
    padding: 0 125px 0 0 !important;
    font: 1rem/1.667 font-family(sans-serif) !important;
    box-shadow: inset 0 0 0 1px #d1d1d1;
    max-width: none !important;

    .sidebar__widgets & {
      padding: 0 48px 0 0 !important;
      box-shadow: inset 0 0 0 1px $poppy;
    }

    .mktoFormRow .mktoLabel, .mktoFormRow .mktoGutter {
      display: none !important;
    }

    input[type="email"] {
      background: none;
      border: none !important;
      outline: none !important;
      padding: 11px 15px !important;
      width: 100% !important;
      font-size: rem(15);
      line-height: 1.2;
    }

    button[type="submit"] {
      transition: background 0.3s linear;
      position: absolute;
      right: 0;
      bottom: 0;
      top: 0;
      width: 100%;
      background: #d1d1d1 !important;
      border: none !important;
      outline: none;
      border-radius: 0;
      cursor: pointer;
      color: $white;
      font-size: rem(16);
      font-weight: 700;
      line-height: 1;

      &::before {
        content: "Sign Up";
      }

      .sidebar__widgets & {
        background: $poppy url('data:image/svg+xml;base64,') no-repeat center/14px 14px !important;
        width: 42px;

        &::before {
          content: none;
        }
        &:hover {
          background: #b06613 url('data:image/svg+xml;base64,') no-repeat center/14px 14px !important;
          border: none !important;
        }
      }

      img {
        display: none !important;
      }

      &:hover {
        background: $poppy !important;
        border: none !important;
      }
    }

    .mktoFormCol {
      margin: 0 !important;
    }

    .mktoButtonRow {
      position: absolute !important;
      right: 0;
      height: 100%;
      width: 125px !important;
    }

    .mktoButtonWrap {
      position: absolute;
      right: 0;
      height: 100%;
      width: 100%;
      top: 0;
    }

    &.mktoForm .mktoButtonWrap.mktoSimple .mktoButton {
      border: 0 none !important;
    }
  }

  &.widget--subscribe-inline {
    > div {
      margin-bottom: 0;
    }

    .mktoFormRow {
      display: none;

      &:first-of-type {
        display: block;
      }
    }

    .mktoButtonRow {
      top: 0;
    }
  }
}

.sidebar__widgets .widget--subscribe {
  .widget__title {
    display: none !important;
  }

  p {
    display: none !important;
  }

  .mktoForm {
    padding: 0 48px 0 0 !important;
    box-shadow: inset 0 0 0 1px $poppy;

    button[type="submit"] {
      background: $poppy url('data:image/svg+xml;base64,') no-repeat center/14px 14px !important;
      width: 42px;

      &::before {
        content: none;
      }

      &:hover {
        background: #b06613 url('data:image/svg+xml;base64,') no-repeat center/14px 14px !important;
        border: none !important;
      }
    }
  }
}


.thankyou-message,
.wp-content .thankyou-message {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 0;

  h4 {
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 36px;
    text-align: center;
    color: $poppy;
  }

  p {
    font-weight: 300;
    font-size: 16px;
    line-height: 26px;
    text-align: center;
    color: $steel;
    margin-bottom: 0;
  }

  .thankyou-message-inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    align-items: flex-start;
    padding-left: 65px;
    padding-top: 20px;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 45px;
      height: 45px;
      background: url('data:image/png;base64,') no-repeat 50% 50% / contain;
    }

    .widget--subscribe-sidebar & {
      padding-left: 0;
      padding-top: 58px;

      &::before {
        left: 50%;
        bottom: auto;
        top: 0;
        margin-left: -20px;
        width: 40px;
        height: 40px;
      }

      h4 {
        text-align: center;
        font-size: 22px;
      }
    }
  }
}

.thankyou-message-sidebar {
  align-items: center;

  h4 {
    margin-bottom: 15px;
  }

  .thankyou-message-inner {
    align-items: center;
    padding-left: 0;

    &::before {
      left: 50%;
      bottom: 100%;
      margin-left: -22px;
    }
  }
}

.thankyou-message-header {
  align-items: flex-start;

  .thankyou-message-inner {
    align-items: flex-start;
  }
}

.widget--subscribe {
  .thankyou-message-inner {
    h4 {
      margin-bottom: 0;
    }
  }

  .mktoForm {
    .mktoFormRow .mktoLabel, .mktoFormRow .mktoGutter {
      display: none !important;
    }

    .mktoFormRow{
      position: relative !important;
      width: 100% !important;

      .sidebar__widgets & {
        display: none;

        &:first-of-type {
          display: block;
        }
      }
    }
  }

  .input-wrap {
    .mktoForm {
      max-width: 320px;
      .mktoFormRow {
        display: none !important;

        &:first-of-type {
          display: block !important;
        }
      }
    }
  }
}
/* stylelint-enable */
