/*
 * Site Footer
 */

.site-footer {
	display: flex;
	flex-direction: column;
	margin-top: auto;

	@include breakpoints(small) {
		flex-direction: row;
		padding: rem(0 36 36);
	}

	@include breakpoints(large) {
		margin-top: 0;
		padding: 0 0 spacer(3);
	}
}

.site-footer__utilities-menu li,
.site-footer__copyright {
	align-items: center;
	border-top: $border-light;
	color: $steel;
	display: flex;
	height: $footer-line-height;
	line-height: 1;
	padding: rem(8) rem(20);
	font-size: rem(14);

	@include breakpoints(small) {
		border-top: none;
		padding: 0;
		height: auto;
		font-size: rem(12);
	}
}

.site-footer__utilities-menu,
.site-footer__social-media-menu {
	@include breakpoints(small) {

		.menu {
			display: flex;
		}
	}
}

.site-footer__utilities-menu {
	@include hide-submenu;

	a {
		@include link-hover;
		color: $steel;
	}

	@include breakpoints(small) {
		margin-left: auto;
		order: 1;

		li {
			@include separator();
		}
	}
}

.site-footer__social-media-menu {
	display: none;

	li {
		margin-right: spacer(1);
	}

	a {
		color: $steel;
		font-size: rem(12);
		line-height: 1;
		text-decoration: none;

		&[href*="twitter.com"],
		&[href*="soundcloud.com"],
		&[href*="simplecast.com"],
		&[href*="youtube.com"] {
			@include text-replace(rem(18), rem(15));
			background: center no-repeat;
			background-size: 100% auto;

			@supports (mask-image: url()) {
				background-color: fade-out( $steel, 0.5);
				mask-position: center;
				mask-repeat: no-repeat;
				mask-size: 100% auto;
			}
		}

		&[href*="twitter.com"] {
			background-image: url('../../images/icon-twitter.svg');

			@supports (mask-image: url()) {
				background-image: none;
				mask-image: url('../../images/icon-twitter.svg');
			}
		}

		&[href*="soundcloud.com"] {
			background-image: url('../../images/icon-soundcloud.svg');
			width: rem(34);

			@supports (mask-image: url()) {
				background-image: none;
				mask-image: url('../../images/icon-soundcloud.svg');
			}
		}

		&[href*="simplecast.com"] {
			background-image: url('../../images/icon-simplecast.svg');
			width: rem(18);

			@supports (mask-image: url()) {
				background-image: none;
				mask-image: url('../../images/icon-simplecast.svg');
			}
		}

		&[href*="youtube.com"] {
			background-image: url('../../images/icon-youtube.svg');
			width: rem(24);

			@supports (mask-image: url()) {
				background-image: none;
				mask-image: url('../../images/icon-youtube.svg');
			}
		}
	}

	@include breakpoints(small) {
		display: block;
	}
}

.site-footer__copyright {
	@include breakpoints(small) {
		order: -1;
		margin-right: spacer(2);
	}
}

// stylelint-disable selector-max-specificity

.footer-new {
	padding: rem(45 0);
	font-size: 13px;
	line-height: 1.23;
	color: $steel;
	font-weight: 300;
	margin-top: rem(45);

	.page-template-page-templatespage_template-home-php & {
		display: block;
	}

	@include breakpoints(s-medium) {
		padding: rem(31 0);
		margin-top: rem(31);
	}

  .footer-above,
  .footer-above--mobile {
    font-size: 0.813rem;
    line-height: 1;
    margin-bottom: 0.5rem;
    color: $concrete;
  }

  .footer-above--mobile {
    text-align: center;
    margin-top: -0.25rem;
    width: 100%;
    display: block;

    @include breakpoints(s-medium) {
      display: none;
    }
  }

	.footer-above {
    @include breakpoints(none, s-medium) {
      display: none;
    }
  }

	.container {
		display: flex;
		flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
	}

	.footer-l {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		order: 2;
		width: 100%;

		@include breakpoints(s-medium) {
			order: 1;
			width: auto;
      padding-right: rem(20);
      padding-top: rem(20);
		}

    @include breakpoints(s-large) {
      padding-top: 0;
      padding-right: 0;
    }
	}

	.copyright {
		display: block;
		width: 100%;
		text-align: center;
		margin-bottom: rem(33);

		@include breakpoints(s-medium) {
			margin: rem(0 32 0 0);
			text-align: left;
			width: auto;
		}
	}

	.social-networks {
		padding: 0;
		list-style: none;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		width: 100%;

		@include breakpoints(s-medium) {
			justify-content: start;
			width: auto;
			margin: 0 -10px;
		}

		li {
			padding: 0 10px;

			a {
				transition: color 0.3s linear;
				display: block;
				text-decoration: none;
				color: rgba(64, 71, 78, 0.5);
				font-size: 16px;

				&:hover {
					color: $poppy;
				}

				&.icon-podcast {
					font-size: 18px;
				}
			}
		}
	}

	.menu-social-media-container {
		width: 100%;
	}

	#social-media-menu {
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: center;
	}

	.site-footer__social-media-menu li {
		margin: 0;
	}
}

.footer-nav {
	order: 1;
	width: 100%;
  margin-bottom: rem(8);

	@include breakpoints(s-medium) {
		order: 2;
		margin-bottom: 0;
		width: auto;
    padding-top: rem(20);
	}

  @include breakpoints(s-large) {
    padding-top: 0;
  }

	ul {
		margin: 0 -15px;
		padding: 0;
		list-style: none;
		text-align: center;

		@include breakpoints(s-medium) {
			display: flex;
			flex-wrap: wrap;
			text-align: left;
		}

		@include breakpoints(s-large) {
			justify-content: flex-end;
		}

		li {
			padding: 0 15px;
			position: relative;
			margin-bottom: rem(23);

			@include breakpoints(s-medium) {
				margin-bottom: 0;
			}

			+ li::before {
				left: 0;
				top: 4px;
				bottom: 4px;
				width: 1px;
				background: $steel;
				position: absolute;

				@include breakpoints(s-medium) {
					content: "";
				}
			}

			a {
				transition: color 0.3s linear;
				text-decoration: none;
				color: $steel;

				&:hover {
					color: $poppy;
				}
			}
		}
	}
}

.footer-future {
	background: $black;
	color: rgba($white,0.8);
	padding: 98px 0 72px;
	text-align: center;
	font-size: rem(26);
	line-height: 1.307;
    letter-spacing: -0.04em;

    @include breakpoints(none, s-large) {
      font-size: rem(22);
      letter-spacing: 0;
    }

    @include breakpoints(none, small) {
    	padding: 60px 0;
    	font-size: rem(20);
    }

	.logo-wrap {
		width: 177px;
		margin: 0 auto 30px;
	}

	h2 {
		font-size: rem(36);
		line-height: 1;
        letter-spacing: 0;
		color: $white;
        font-weight: 700;
		margin-bottom: 16px;

		@include breakpoints(none, s-large) {
	      font-size: rem(30);
	    }

	    @include breakpoints(none, small) {
	      font-size: rem(26);
	    }
	}

	.btn-wrap {
		margin-top: 35px;

		.btn-future-blue {
			font-weight: 500;
            background: $future_blue;

            &:hover {
              background: darken($future_blue, 10);
            }
		}
	}
}
// stylelint-enable selector-max-specificity
